<template>
  <div class="router_view">
    <el-container>
      <el-aside style="width:200px;overflow:inherit" class="sidebar">
        <sidebar></sidebar>
      </el-aside>
      <el-container>
        <!-- <el-header>
          <topbar></topbar>
        </el-header> -->
        <el-main>
          <router-view></router-view>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
import sidebar from './Sidebar'
import bus from './bus.js'
import $ from 'jquery'
export default {
  data () {
    return {
      sideWidth: ''
    }
  },
  components: {
    sidebar: sidebar

  },
  mounted () {
    bus.$on('width', w => {
      $('.sidebar').animate({
        width: w
      })
      this.sideWidth = w
    })
  },
  watch: {
    '$route' (to, from) {}
  }

}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .title {
    text-align: left;
    margin-left: 20px;
  }

  .sidebar {
    background-color: rgb(84, 92, 100);
    display: inline-block;
    max-height: 100vh;
    overflow-y: hidden !important;
    overflow-x: hidden !important;
  }
  .content {
    padding-top: 115px;
    margin-left: 18%;
  }

  .el-menu-vertical-demo {
    position: fixed;
    width: 18%;
  }

  .el-menu-vertical-demo {
    position: relative;
    width: 100%;
  }

  .el-header {
    padding: 0;
  }

  .el-main {
    height: calc(100vh - 115px);
    /* margin-top: 25px; */
  }

</style>



