<template>
  <div class="side">
    <!-- <div class="title">柜台</div> -->
  <el-menu
      default-active="1"
      class="el-menu-vertical-demo"
      @open="handleOpen"
      @close="handleClose"
      background-color="#fff"
      text-color="#333"
      active-text-color="#ffd04b">
      <router-link to="registration">
      <el-menu-item index="1">
        <i class="el-icon-d-caret"></i>
        <span slot="title">挂号</span>
      </el-menu-item>
        </router-link>
     <el-menu-item index="2">
        <i class="el-icon-setting"></i>
        <span slot="title">收银</span>
      </el-menu-item>
         <el-menu-item index="3">
        <i class="el-icon-phone"></i>
        <span slot="title">预约</span>
      </el-menu-item>
         <el-menu-item index="4">
        <i class="el-icon-edit"></i>
        <span slot="title">会员管理</span>
      </el-menu-item>
       <el-menu-item index="9">
        <i class="el-icon-news"></i>
        <span slot="title">患者中心</span>
      </el-menu-item>
         <el-menu-item index="5">
        <i class="el-icon-service"></i>
        <span slot="title">客服</span>
      </el-menu-item>
         <el-menu-item index="6">
        <i class="el-icon-tickets"></i>
        <span slot="title">疗程</span>
      </el-menu-item>
         <el-menu-item index="7">
        <i class="el-icon-date"></i>
        <span slot="title">定制功能</span>
      </el-menu-item>
         <el-menu-item index="8">
        <i class="el-icon-service"></i>
        <span slot="title">分诊台</span>
      </el-menu-item>
    </el-menu>
    <!-- <div id="aa" @click="aaa">按钮</div> -->
  </div>
</template>
<script>
export default {
  data () {
    return {

    }
  },
  methods: {
    handleOpen (key, keyPath) {
      console.log(key, keyPath)
    },
    handleClose (key, keyPath) {
      console.log(key, keyPath)
    }
  },
  props: {
    menu: Array
  }
}

</script>
<style>
*{
  margin: 0;
  padding: 0;
}
.el-menu-item{
  text-align: left;
  border: none;
}
.sidebar-menu {
  min-height: 100vh;
}

.side-menu:not(.el-menu--collapse) {
  width: 200px;
}
.sidebar{
min-height: 100vh;
background-color: #fff !important;
border-right: 2px solid #ccc;
}
.el-menu{
  border: none;
}
.el-menu--collapse {
  width: 54px;
  min-height: 100vh;
}

.router-default {
  text-decoration: none;
  padding: 10px 80px 10px 8px;
  color: #fff;
}
.side a{
  display: inline-block;
    text-decoration: none;
    color: #fff;
    width: 100%;
}
.side .title{
  height: 45px;
  line-height: 45px;
  text-align: left;
  padding-left: 30px;
  border-bottom: 1px solid #ccc;
}

</style>



